body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #c4b28e;
}
.container {
  position: relative;
  padding: 0 30px;
  height: 100vh;
  background-image: url("../img/background/bg-bamboo-mobile@1x.jpg");
}
@media (min-resolution: 2dppx) {
  .container {
    background-image: url("../img/backgrounds/bg-bamboo-mobile@2x.jpg");
  }
}
@media (min-width: 660px) {
  .container {
    background-image: url("../img/background/bg-bamboo-tablet@1x.jpg");
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .container {
    background-image: url("../img/backgrounds/bg-bamboo-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    margin: 0 auto;
    background-image: url("../img/background/bg-bamboo-desktop@1x.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .container {
    background-image: url("../img/backgrounds/bg-bamboo-desktop@2x.jpg");
  }
}
.monitor {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  margin: 0;
  padding: 0;
}
@media (min-width: 660px) {
  .monitor {
    bottom: 50px;
    width: 660px;
    height: auto;
  }
}
@media (min-width: 1200px) {
  .monitor {
    bottom: 0;
    width: 1080px;
  }
}
.shelf {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  margin: 0;
  padding: 0;
}
@media (min-width: 660px) {
  .shelf {
    top: 50px;
    width: 660px;
    height: auto;
  }
}
@media (min-width: 1200px) {
  .shelf {
    top: 5px;
    width: 1080px;
  }
}
.sticker {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  margin: 0;
  padding: 0;
  height: auto;
}
@media (min-width: 660px) {
  .sticker {
    bottom: 330px;
    width: 55px;
  }
}
@media (min-width: 1200px) {
  .sticker {
    bottom: 462px;
    width: 85px;
  }
}
.sticker-inst,
.sticker-arrow,
.sticker-pic {
  display: block;
  margin: 0;
  padding: 0;
  height: auto;
}
@media (min-width: 660px) {
  .sticker-inst {
    width: 65px;
  }
}
@media (min-width: 1200px) {
  .sticker-inst {
    width: 95px;
  }
}
.sticker-arrow {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 660px) {
  .sticker-arrow {
    bottom: 339px;
    width: 20px;
  }
}
@media (min-width: 1200px) {
  .sticker-arrow {
    bottom: 475px;
    width: 33px;
  }
}
@media (min-width: 660px) {
  .sticker-pic {
    width: 85px;
  }
}
@media (min-width: 1200px) {
  .sticker-pic {
    width: 110px;
  }
}
@media (min-width: 660px) {
  .links-container {
    position: relative;
    top: 45vh;
    width: 260px;
    height: 100px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .links-container {
    position: static;
  }
}
.link-inst {
  position: absolute;
  cursor: pointer;
}
@media (min-width: 660px) {
  .link-inst {
    right: 32px;
    bottom: -15px;
  }
}
@media (min-width: 1200px) {
  .link-inst {
    left: 699px;
    bottom: 330px;
  }
}
.link-pictures {
  position: absolute;
  cursor: pointer;
}
@media (min-width: 660px) {
  .link-pictures {
    left: 17px;
    bottom: -19px;
  }
}
@media (min-width: 1200px) {
  .link-pictures {
    left: 449px;
    bottom: 320px;
  }
}
